@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
}

@-ms-viewport {
  width: device-width;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: "Noto Sans Japanese", sans-serif;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  color: #000;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: .5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: normal;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #0275d8;
  text-decoration: none;
  background-color: transparent;
}

a:hover, a:focus {
  color: #5bc0de;
  text-decoration: underline;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

/* ==================================================
   フォーム
 ================================================== */
.form-control {
  display: block;
  width: 100%;
  padding: .5rem .75rem;
  font-size: 1rem;
  line-height: 1.25;
  color: #495057;
  background-color: #fff;
  background-image: none;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

.form-control::-ms-expand {
  background-color: transparent;
  border: 0;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: none;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}

.form-control::placeholder {
  color: #868e96;
  opacity: 1;
}

.form-control:disabled, .form-control[readonly] {
  background-color: #e9ecef;
  opacity: 1;
}

select.form-control:not([size]):not([multiple]) {
  height: calc(2.25rem + 2px);
}

select.form-control:focus::-ms-value {
  color: #495057;
  background-color: #fff;
}

.form-control-file,
.form-control-range {
  display: block;
}

.col-form-legend {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0;
  font-size: 1rem;
}

.form-control-plaintext {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0;
  line-height: 1.25;
  background-color: transparent;
  border: solid transparent;
  border-width: 1px 0;
}

.form-group {
  margin-bottom: 1rem;
}

.form-text {
  display: block;
  margin-top: .25rem;
}

.form-check {
  position: relative;
  display: block;
  margin-bottom: .5rem;
}

.form-check.disabled .form-check-label {
  color: #868e96;
}

.form-check-label {
  padding-left: 1.25rem;
  margin-bottom: 0;
}

.form-check-input {
  position: absolute;
  margin-top: .25rem;
  margin-left: -1.25rem;
}

.form-check-input:only-child {
  position: static;
}

.form-check-inline {
  display: inline-block;
  margin-right: .25rems;
}

.form-check-inline + .form-check-inline {
  margin-left: .75rem;
}

.form-check-inline .form-check-label {
  vertical-align: middle;
}

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline .form-check {
  width: 100%;
}

.form-inline label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
}

.form-inline .form-group {
  display: flex;
  flex: 0 0 auto;
  flex-flow: row wrap;
  align-items: center;
  margin-bottom: 0;
}

.form-inline .form-control {
  display: inline-block;
  width: auto;
  vertical-align: middle;
}

.form-inline .form-control-plaintext {
  display: inline-block;
}

.form-inline .input-group {
  width: auto;
}

.form-inline .form-control-label {
  margin-bottom: 0;
  vertical-align: middle;
}

.form-inline .form-check {
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  margin-top: 0;
  margin-bottom: 0;
}

.form-inline .form-check-label {
  padding-left: 0;
}

.form-inline .form-check-input {
  position: relative;
  margin-top: 0;
  margin-right: .25rem;
  margin-left: 0;
}

.input-group-addon:not(:first-child):not(:last-child),
.input-group-btn:not(:first-child):not(:last-child),
.input-group .form-control:not(:first-child):not(:last-child) {
  border-radius: 0;
}

.input-group-addon,
.input-group-btn {
  vertical-align: middle;
  white-space: nowrap;
}

.input-group-addon {
  padding: 0.5rem 0.75rem;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.25;
  color: #495057;
  text-align: center;
  background-color: #e9ecef;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.input-group-addon input[type="radio"],
.input-group-addon input[type="checkbox"] {
  margin-top: 0;
}

.input-group .form-control:not(:last-child),
.input-group-addon:not(:last-child),
.input-group-btn:not(:last-child) > .btn,
.input-group-btn:not(:last-child) > .btn-group > .btn,
.input-group-btn:not(:last-child) > .dropdown-toggle,
.input-group-btn:not(:first-child) > .btn:not(:last-child):not(.dropdown-toggle),
.input-group-btn:not(:first-child) > .btn-group:not(:last-child) > .btn {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.input-group-addon:not(:last-child) {
  border-right: 0;
}

.input-group .form-control:not(:first-child),
.input-group-addon:not(:first-child),
.input-group-btn:not(:first-child) > .btn,
.input-group-btn:not(:first-child) > .btn-group > .btn,
.input-group-btn:not(:first-child) > .dropdown-toggle,
.input-group-btn:not(:last-child) > .btn:not(:first-child),
.input-group-btn:not(:last-child) > .btn-group:not(:first-child) > .btn {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.form-control + .input-group-addon:not(:first-child) {
  border-left: 0;
}

/* ==================================================
   ボタン
 ================================================== */
.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  line-height: 1.25;
  border-radius: .25rem;
  cursor: pointer;
  transition: all .15s ease-in-out;
}

.btn:focus, .btn:hover {
  text-decoration: none;
}

.btn:focus, .btn.focus {
  outline: 0;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.25);
}

.btn.disabled, .btn:disabled {
  opacity: .65;
  box-shadow: none;
}

.btn:active, .btn.active {
  background-image: none;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}

/* ==================================================
   page-example.html
 ================================================== */
.wrap {
  width: 100%;
  padding: 0 14px;
}

@media (min-width: 980px) {
  .wrap {
    width: 980px;
    margin: 0 auto;
  }
}

.page-example.is-page-example {
  overflow: hidden;
}

/* --------------------------------------------------
   メニュー全体
 -------------------------------------------------- */
.menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 70px;
  background-color: #fff;
}

@media (min-width: 768px) {
  .menu {
    position: static;
    height: auto;
  }
}

/* --------------------------------------------------
   SP版メニューを開いたときに背景となる
 -------------------------------------------------- */
.menu-inner.is-menu-inner {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.9);
  overflow: scroll;
}

/* --------------------------------------------------
   メニューボタン
 -------------------------------------------------- */
.menu-btn {
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 52px;
  height: 52px;
  padding: 0;
  background-color: #fff;
  border: none;
  z-index: 2;
}

.menu-btn:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .menu-btn {
    display: none;
  }
}

.menu-btn_line {
  display: inline-block;
  position: absolute;
  left: 5px;
  width: 40px;
  height: 2px;
  background-color: #0275d8;
}

.menu-btn_line:nth-child(1) {
  top: 16px;
}

.menu-btn_line:nth-child(2) {
  top: 25px;
}

.menu-btn_line:nth-child(3) {
  top: 34px;
}

/* --------------------------------------------------
  メインナビゲーション
-------------------------------------------------- */
.gnav {
  display: none;
}

.is-menu-inner .gnav {
  display: block;
  padding: 70px 0 0 0;
}

@media (min-width: 768px) {
  .gnav {
    display: block;
  }
}

.gnav ul {
  padding-left: 0;
  list-style: none;
  border-top: 1px solid #fff;
}

@media (min-width: 768px) {
  .gnav ul {
    display: flex;
    border-top: none;
  }
}

.gnav li {
  border-bottom: 1px solid #fff;
}

@media (min-width: 768px) {
  .gnav li {
    flex: 1 0 20%;
    text-align: center;
    border-bottom: none;
  }
}

.gnav a {
  display: block;
  padding: 14px 10px;
  background-color: #0275d8;
  color: #fff;
}

@media (min-width: 768px) {
  .gnav a {
    padding: 10px 0;
    background-color: transparent;
    color: #0275d8;
  }
}

.gnav a:hover, .gnav a:focus {
  background-color: #5bc0de;
  text-decoration: none;
}

@media (min-width: 768px) {
  .gnav a:hover, .gnav a:focus {
    background-color: transparent;
    text-decoration: underline;
  }
}

/* ==================================================
   page-example2.html （アニメーション/Grid 確認用）
 ================================================== */
/* --------------------------------------------------
   Grid
 -------------------------------------------------- */
@media (min-width: 768px) {
  .magazine {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto auto auto;
  }
}

.magazine-item {
  margin: 0 0 40px 0;
}

@media (min-width: 768px) {
  .magazine-item {
    margin: 0 5px 40px 5px;
  }
}

.magazine-today {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}

.magazine-past01 {
  grid-column: 3 / 4;
  grid-row: 1 / 2;
}

.magazine-past02 {
  grid-column: 4 / 5;
  grid-row: 1 / 2;
}

.magazine-past03 {
  grid-column: 1 / 2;
  grid-row: 3 / 4;
}

.magazine-past04 {
  grid-column: 2 / 3;
  grid-row: 3 / 4;
}

.magazine-pickup {
  grid-column: 3 / 5;
  grid-row: 2 / 3;
}

.magazine-today__img,
.magazine-item__img,
.magazine-pickup__img {
  margin: 0 0 6px 0;
}

.magazine-today__img img,
.magazine-item__img img,
.magazine-pickup__img img {
  max-width: 100%;
  height: auto;
}

.magazine-today__title,
.magazine-item__title,
.magazine-pickup__title {
  margin: 0 0 4px 0;
}

/* --------------------------------------------------
   個別ページフアニメーション
 -------------------------------------------------- */
@keyframes fade-in-load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.magazine-today__img,
.magazine-item__img,
.magazine-pickup__img {
  animation: fade-in-load 3s ease-in-out;
}

/* ==================================================
   ユーティリティ
 ================================================== */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  overflow: visible;
  clip: auto;
  white-space: normal;
  clip-path: none;
}

.text-hide {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.clearfix::after {
  display: block;
  content: "";
  clear: both;
}

.tac {
  text-align: center;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.img-widemax {
  width: 100%;
  height: auto;
}

@keyframes fadein-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

